*{
  padding:0;
  margin:0;
}
ul,li{
  list-style: none;
}

/*header*/
body{
  background-color: #ddd;
}
.headerwrap{
  background: #444;
  width:100%;
  height: 50px;
}


.headerwrap header{
  width:1200px;
  margin:0 auto;
  
}
.headerwrap img{
  width:120px;
  margin-top: 9px;
}

.headerwrap a{
  margin: 0 20px;
  color: #fff;
}
/* main */
.main{
  width: 1200px;
  margin:30px auto;
}
.content{
  width:900px;
  border:1px solid none;
  background-color:white;
  float: left;
  margin-bottom: 17px;
}
.sider{
  float: right;
  width: 270px;
  height: 1550px;
  /* background-color: green; */
}
.bottomDiv{
  width: 100%;
  background-color: white;
  height: 200px;
  float: left;
  padding-left: 158px;
  padding-top: 20px;
}
.innerOne{
  width: 270px;
  height: 120px;
  background-color:white;
  float: left;
  margin-bottom: 15px;
  padding-top: 10px;
  padding-left: 10px;
}
.innerTwo{
  width: 270px;
  height: 235px;
  background-color: white;
  float: left;
  margin-bottom: 15px;
  text-align: center;
}
.innerTwob{
  width: 270px;
  height: 235px;
  background-color: white;
  float: left;
  margin-bottom: 15px;
  font-size: 13px;
}
.innerTwob p{
  margin-left: 10px;
  margin-top: 15px;
}

.innerTwo img{
  width: 250px;
  height: 65px;
  margin-top: 10px;
}
.innerThree{
  width: 270px;
  height: 330px;
  background-color: white;
  float: left;
  margin-bottom: 15px;
}
.innerThree ul{
  margin-left: 10px;
  margin-top: 15px;
}
.innerThree li{
  margin-bottom: 5px;
}
.innerFour{
  width: 270px;
  height: 280px;
  background-color:white;
  float: left;
  margin-bottom: 15px;
}
.innerFour img{
  width: 180px;
  height: 45px;
  margin-left: 10px;
  margin-top: 15px;
}
.innerFive{
  width: 270px;
  height: 270px;
  background-color: white;
  float: left;
  margin-bottom: 15px;
}
.header{
  width: 270px;
  height: 40px;
  background-color: whitesmoke;
  line-height: 40px;
  padding-left: 10px;
}
.innerFive img{
  width: 180px;
  height: 180px;
  margin-top: 10px;
  margin-bottom: 7px;
  margin-left: 45px;
}
.innerFive p{
  margin-left: 85px;
}
.login{
  width: 150px;
  height: 34px;
  cursor: pointer;
  background-color: turquoise;
  border: none;
  border-radius: 5px;
  color: white;
}

.bottomDiv img{
  width:100px;
  height: 30px;
}


